import React, { useMemo } from "react";
import WithFilter from "../../screens/common/WithFilter";
import CompanySettingsContent from "../../screens/CompanySettings/CompanySettingsContent";
import CompanySettingsHeader from "../../screens/CompanySettings/components/CompanySettingsHeader";
import CompanySettingsFooter from "../../screens/CompanySettings/components/CompanySettingsFooter";
import CompanySettingsFilters from "../../screens/CompanySettings/components/ComanySettingsHeaderFilter";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const CompanySettingsPage = () => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <WithFilter
      filterElement={<CompanySettingsFilters />}
      contentElement={<CompanySettingsContent />}
      filterTitle={t('translation:default.fields.filter')}
      pageTitle={t('translation:default.fields.manageCompany')}
      headerContent={<CompanySettingsHeader />}
      footerContent={<CompanySettingsFooter />}
    />
  );
};

export default CompanySettingsPage;
