import React , { useMemo }from "react";
import ScrollPagination from "../../components/Paginations/ScrollPagination";
import _ from "lodash";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const ComapnySettingsContent = () => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <div className="h-100 pt-15 d-flex flex-direction-column">
      <ScrollPagination> {t('translation:default.fields.manageCompany')}</ScrollPagination>
    </div>
  );
};

export default ComapnySettingsContent;
